  float: left;
  width: 30px;
  height: 30px;
  background: url('data:image/png;base64,') top right no-repeat;
  text-align: right;
  outline: none;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=70);
  opacity: 0.7;
  transition: opacity 0.2s;
}

.lb-data .lb-turnRight {
  display: block;
  float: right;
  width: 30px;
  height: 30px;
  background: url('data:image/png;base64,') top right no-repeat;
  text-align: right;
  outline: none;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=70);
  opacity: 0.7;
  transition: opacity 0.2s;
}

.lb-data .lb-zoomOut {
  display: block;
  float: left;
  width: 30px;
  height: 30px;
  background: url('data:image/png;base64,') top right no-repeat;
  text-align: right;
  outline: none;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=70);
  opacity: 0.7;
  transition: opacity 0.2s;
}

.lb-data .lb-zoomIn {
  display: block;
  float: right;
  width: 30px;
  height: 30px;
  background: url('data:image/png;base64,') top right no-repeat;
  text-align: right;
  outline: none;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=70);
  opacity: 0.7;
  transition: opacity 0.2s;
}

.lb-data .lb-close:hover {
  cursor: pointer;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}

/* animation */

@keyframes fadeIn{    
  0% {opacity: 0;}
  100% {opacity: 1;}
}

@keyframes fadeOut{    
  0% {opacity: 1;}
  100% {opacity: 0;}
}

@keyframes fadeInOverlay{    
  0% {opacity: 0;}
  100% {opacity: 0.8;}
}

@keyframes fadeOutOverlay{    
  0% {opacity: 0.8;}
  100% {opacity: 0;}
}

.fadeIn{
  animation-name: fadeIn; 
}

.fadeInOverlay{
  animation-name: fadeInOverlay;
}

.fadeOut{
  animation-name: fadeOut;
}

.fadeOutOverlay{
  animation-name: fadeOutOverlay;
}

.animation{
  animation-fill-mode: both;
}

.transition{
  /* For Safari 3.1 to 6.0 */
  -webkit-transition-property: all;
  -webkit-transition-timing-function: ease;
  /* Standard syntax */
  transition-property: all;
  transition-timing-function: ease;
}

.lb-image {
